<template>
    <div class="vuecal">
        <div>
            <my-input class="search" placeholder="ФИО Студента" v-model="Search"></my-input>
        </div>
        <vue-cal
                :time="false"
                locale="ru"
                :disable-views="['years','year','week']"
                class="vuecal--green-theme"
                :events="Events"
                :selected-date="selectedDate"
                events-dash-on-year-view
                style="height: 500px"
                @event-click="EventClick"
        >

        </vue-cal>

        <my-dialog v-model:show="show">
            <h1>Проставление оценок</h1>
            <div class="dialog">ФИО студента: {{ this.chosenUser.surname }} {{this.chosenUser.name}}
                <span v-if="this.chosenUser.secondname!==undefined">{{ this.chosenUser.secondname }}</span>
            </div>
            <div v-if="labGrade != null">
                <span v-if="!this.labGrade.gradeConfirmed">Промежуточная оценка: {{ grades[this.labGrade.rating] }}</span>
                <span v-if="this.labGrade.gradeConfirmed">Окончательная оценка: {{ grades[this.labGrade.rating] }}</span>
            </div>
            <div class="dialog">Описание задачи: {{ `${this.task.description}` }}</div>
            <my-select :options="options" v-model="chosen">

            </my-select>
            <div>
                <v-btn @click="change" color="grey" style="margin-top: 15px;">Поставить промежуточную оценку</v-btn>
            </div>
            <div>
                <v-btn @click="changeConfirmed" color="grey" style="margin-top: 15px;">Поставить окончательную оценку
                </v-btn>
            </div>
            <div v-if="errorCode !== ''">
                Ошибка: {{errorCode}}
            </div>
        </my-dialog>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService";
    import UserService from "@/service/UserService";

    export default {
        data() {
            return {
                events: [],
                selectedDate: undefined,
                Search: '',
                show: false,
                grades: {
                    "1": "Неявка",
                    "2": "Неудовлетворительно",
                    "3": "Удовлетворительно",
                    "4": "Хорошо",
                    "5": "Отлично"
                },
                options: [
                    {text: "Неявка", value: "1"},
                    {text: "Неудовлетворительно", value: "2"},
                    {text: "Удовлетворительно", value: "3"},
                    {text: "Хорошо", value: "4"},
                    {text: "Отлично", value: "5"},
                ],
                chosen: '1',
                user: {},
                task: {},
                chosenUser: {},
                chosenLabuserId: undefined,
                labGrade: undefined,
                errorCode: ''
            }
        },
        mounted() {
            this.selectedDate = new Date()
            this.user = this.$store.getters['registration/User'];
            this.getAllLabUsers()
        },
        methods: {
            async getAllLabUsers() {
                try {
                    const response = await PostsService.getAllLabUsers()
                    this.events = response.data
                } catch (e) {
                    this.errorCode = e
                }
            },
            EventClick(e) {
                this.task = e.labUser.task
                this.chosenLabuserId = e.labUser.id
                this.chosenUser = e.labUser.user
                this.labGrade = e.labUser.labGrade
                this.show = true
            },
            async change(){
                try {
                    const grade = {
                        rating: Number.parseInt(this.chosen),
                        teacherId: this.user.id,
                        labUserId: this.chosenLabuserId
                    }
                    const request = {
                        labId: this.chosenLabuserId,
                        grade: grade
                    }
                    const response = await UserService.estimate(request)
                    this.show = false;
                } catch (e) {

                    this.errorCode = 'Студенту уже поставлена оценка'
                    console.log(e.response?.data?.message);
                }
                await this.getAllLabUsers()
            },
            async changeConfirmed(){
                try {
                    const grade = {
                        rating: Number.parseInt(this.chosen),
                        teacherId: this.user.id,
                        labUserId: this.chosenLabuserId,
                        gradeConfirmed: true
                    }
                    const request = {
                        labId: this.chosenLabuserId,
                        grade: grade
                    }
                    const response = await UserService.estimate(request)
                    this.show = false;
                } catch (e) {

                    this.errorCode = 'Студенту уже поставлена оценка'
                    console.log(e.response?.data?.message);
                }
                await this.getAllLabUsers()
            },
        },
        computed: {
            Events() {
                return [...this.events].filter(e => e.user.role === 'user' && `${e.user.name} ${e.user.surname} ${e.user.secondName}`.toLowerCase().includes(this.Search.toLowerCase()))

                    .map(e => ({
                        start: new Date(e.date).toISOString().split('T')[0],
                        end: new Date(e.date).toISOString().split('T')[0],
                        title: e.user.surname,
                        content: e.task.description,
                        labUser: e
                    }))
            }
        }
    }
</script>

<style scoped>
    .search {
        margin: 10px;
    }
</style>