<template>
    <div>
        <v-btn color="grey" @click="getDb">
            Скачать excel(.xls) файл с записями
        </v-btn>
        <div>
            {{message}}
        </div>
        <div v-if="errorCode!==''">
            {{errorCode}}
        </div>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService";

    export default {
        data(){
            return{
                user: {},
                errorCode: '',
                message: ''
            }
        },
        mounted(){
            this.user = this.$store.getters['registration/User'];
        },
        methods: {
            async getDb(){
                try{
                    const email = this.user.email
                    const resp = await PostsService.postDataBase(email)
                    // this.message = resp.data
                    const url = URL.createObjectURL(new Blob([resp.data], {
                        type: 'application/vnd.ms-excel'
                    }))
                    const link = document.createElement('a')
                    link.href = url
                    link.setAttribute('download', `grades-${new Date().toISOString().split('T')[0]}.xls`)
                    document.body.appendChild(link)
                    link.click()
                }
                catch (e) {
                    this.errorCode = e
                    console.log(e)
                }
            }
        }
    }
</script>

