<template>
    <div class="common">
        <div class="leftBar">
            <div class="sting">Фамилия: {{ User.surname }}</div>
            <div class="sting">Имя: {{ User.name }}</div>
            <div class="sting" v-if="User.secondname !== ''">Отчество: {{ User.secondname }}</div>
            <div class="sting">Email: {{ User.email }}</div>
            <div class="sting">Роль: {{ this.role[User.role] }}</div>
        </div>
        <div class="supremum" v-if="User.role === 'user'">
            <div class="bottomBar">
                <div class="row">Мои записи</div>
                <subscribe-list :subscribes="filteredSubscribes" @unsubscribe="choose"></subscribe-list>
            </div>
            <div class="rightBar">
                <div class="row">Мои оценки</div>
                <grade-list :show="true" :grades="grades"></grade-list>
                <div v-if="grades.length === 0">
                    Список оценок пуст
                </div>
            </div>
        </div>
        <div class="v-footer">
            <v-btn @click="logoutA">
                Выход
            </v-btn>
        </div>

        <my-dialog v-model:show="show">
            <div class="text">
                Отписаться от задачи
            </div>
            <v-btn
                    color="grey"
                    @click="unsubscribe">
                Отписаться
            </v-btn>
        </my-dialog>
    </div>

</template>

<script>
    import PostsService from "@/service/PostsService";
    import UserService from "@/service/UserService";
    import VRasp from "@/components/rasp";
    import {mapActions} from "vuex";

    export default {
        components: {VRasp},
        data() {
            return {
                User: this.$store.getters['registration/User'],
                subscribes: [],
                grades: [],
                show: false,
                chosenTask: null,
                role: {
                    "user": "Студент",
                    "teacher": "Преподаватель",
                    "admin": "Администратор"
                },
                showGrades: false,
                chosenDate: undefined
            }
        },
        mounted() {
            this.getSubscribes()
            this.getUser()
            this.getGrades()
        },
        methods: {
            ...mapActions({
                logout: 'registration/logout',
            }),
            async choose(e) {
                this.chosenTask = e
                this.show = true
            },
            async unsubscribe() {
                try {
                    const resp = await PostsService.unsubscribe(this.chosenTask)
                } catch (e) {
                    console.log(e)
                }
                this.show = false

                const index = this.subscribes.findIndex((x) => x.id === this.chosenTask)
                this.subscribes.splice(index, 1)
            },
            async getSubscribes() {
                try {
                    await PostsService.getUserUnconfirmedSubscribes(this.User.id).then(p => {
                            this.subscribes = p.data
                        }
                    )
                } catch (e) {
                    console.log(e)
                }
            },
            async getGrades() {
                try {
                    await PostsService.getGrades(this.User.id).then(p => {
                            this.grades = p.data
                        }
                    )
                } catch (e) {
                    console.log(e)
                }
            },
            getUser() {

            },
            chooseEvent(e){
                this.chosenDate = e
                this.showGrades = true
                this.showStuds = false
            },
            async logoutA() {
                this.logout()
                await this.$router.push({name: 'Registration'})
            }
        },
        computed: {
            filteredSubscribes() {
                const now = new Date()
                const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
                return [...this.subscribes].filter(sub => new Date(sub.date).valueOf() >= today.valueOf())
            },
        }
    }
</script>

<style scoped>
    .leftBar {
        top: 50px;
        right: 50px;
        border: #2c3e50 solid;
        width: 40%;
        height: 50%;
        padding: 10px;

    }

    .sting {
        text-align: left;
        margin-bottom: 5px;
    }

    .common {
        display: block;
    }

    .bottomBar {
        margin: 5px;
        border: #2c3e50 solid;
        width: 50%;
    }

    .rightBar {
        margin: 5px;
        border: #2c3e50 solid;
        width: 50%;
    }

    .list {
        height: 100%;
    }

    .row {
        border: #2c3e50 solid 1px;
    }

    .supremum {
        display: flex;
        justify-content: space-around;
    }

    .text {
        margin: 10px;
    }
</style>