<script>
    import {mapActions} from "vuex";

    export default {
        methods: {
            data() {
                return {
                    user: {},
                }

            },
            ...mapActions({
                logout: 'registration/logout',
            }),
            async goToProfile() {
                await this.$router.push({name: 'profile'})
            },
            async logoutA() {
                this.logout()
                await this.$router.push({name: 'Registration'})
            }
        },
        mounted() {
            const link = this.$route.params.link
            if (link != null) {
                this.activate({link: link})
            }
            this.$store.dispatch('registration/updateUser')
            this.user = this.$store.getters['registration/User'];
        },
        computed: {
            admin() {
                const user = this.$store.getters['registration/User'];

                if (user === null) {
                    return false
                }
                return user.role === 'admin'
            },
            student() {
                const user = this.$store.getters['registration/User'];
                if (user === null) {
                    return false
                }
                return user.role === 'user'
            },
            teacher() {
                const user = this.$store.getters['registration/User'];
                if (user === null) {
                    return false
                }
                return user.role === 'teacher'
            }

        }
    }
</script>

