<template>
    <div class="hh">
        <h1>
          <span class="clickable" @click="goPrevWeek">&lt;</span>
            Неделя {{ week }}
          <span class="clickable" @click="goNextWeek">></span>
        </h1>
    </div>
    <div class="home">
        <div>
            <h1 class="task">
                Понедельник
            </h1>
            <div class="task">
                {{ Monday }}
            </div>
        </div>
        <tasks-list :showDay="false" :tasks="MondayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>

    <div class="home">
        <h1 class="task">Вторник</h1>
        <div class="task">
            {{ Tuesday }}
        </div>
        <tasks-list :showDay="false" :tasks="TuesdayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>

    <div class="home">
        <h1 class="task">Среда</h1>
        <div class="task">
            {{ Wednesday }}
        </div>
        <tasks-list :showDay="false" :tasks="WednusdayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>

    <div class="home">
        <h1 class="task">Четверг</h1>
        <div class="task">
            {{ Thursday }}
        </div>
        <tasks-list :showDay="false" :tasks="ThursdayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>

    <div class="home">
        <h1 class="task">Пятница</h1>
        <div class="task">
            {{ Friday }}
        </div>
        <tasks-list :showDay="false" :tasks="FridayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>

    <div class="home">
        <h1 class="task">Суббота</h1>
        <div class="task">
            {{ Saturday }}
        </div>
        <tasks-list :showDay="false" :tasks="SaturdayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>

    <div class="home">
        <h1 class="task">Воскресенье</h1>
        <div class="task">
            {{ Sunday }}
        </div>
        <tasks-list :showDay="false" :tasks="SundayTasks" @taskChose="taskChose($event)"></tasks-list>
    </div>
        <my-dialog v-model:show="show">
            <h1>Запись на задачу</h1>
            <div class="dialog">Описание задачи: {{ chosenTask.description }}</div>
            <div class="dialog">Количество свободных мест: {{ chosenTask.numOfPoints }}</div>
            <div class="dialog">Записаться на: {{ this.date.toLocaleDateString() }}</div>
            <v-btn @click="subscribe" class="v-btn__COLOR" :loading="loading">Записаться</v-btn>
            <div v-if="IsError">
                Ошибка: {{ errorCode }}
            </div>
        </my-dialog>

    <my-dialog v-model:show="showH">
        <div> {{ errorCode }} </div>
    </my-dialog>

<!--    <v-dialog>-->
<!--        <h1>Запись на задачу</h1>-->
<!--        <div class="dialog">Описание задачи: {{ chosenTask.description }}</div>-->
<!--        <div class="dialog">Количество свободных мест: {{ chosenTask.numOfPoints }}</div>-->
<!--        <div class="dialog">Записаться на: {{ this.date.toLocaleDateString() }}</div>-->
<!--        <my-button @click="subscribe" style="margin-top: 15px;">Записаться</my-button>-->
<!--    </v-dialog>-->


</template>

<script>
    // @ is an alias to /src

    import {mapActions} from "vuex";
    import PostsService from "../service/PostsService";

    export default {
        name: 'Home',
        data() {
            return {
                hour: 'wdw',
                posts: [],
                tasks: [],
                show: false,
                showH: false,
                chosenTaskId: null,
                chosenTask: {},
                date: undefined,
                startWeek: new Date('2022-06-22'),
                endWeek: new Date('2022-06-29'),
                dayTo: {
                    "1": "0",
                    "2": "1",
                    "3": "2",
                    "4": "3",
                    "5": "4",
                    "6": "5",
                    "0": "6",
                },
                loading: false,
                errorCode: ''
            }
        },
        methods: {
            ...mapActions({
                login: 'registration/login',
                get: 'users/getUsers'
            }),
            async getTasks() {
                this.tasks = []
                for (let i = 0; i < 7; i++){
                    let date = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(i))

                    try {
                        const response = await PostsService.getTasks(date.toISOString());
                        this.tasks.push(...response.data);
                    } catch (e) {
                        console.log(e);
                    }
                }

            },
            taskChose(e) {
                if (this.user.role === "user") {
                        this.errorCode = ''
                        this.chosenTask = e
                        this.chosenTaskId = e.id;
                        this.date = this.getDate(this.dayTo[e.day])

                        if (this.validate(e)) {
                            this.show = true;
                        } else {
                            this.showH = true
                        }
                }
            },
            getDate(id) {
                const date = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(id))
                return date
            },
            async subscribe() {
                try {
                    this.errorCode = ''
                    this.loading = true
                    if(this.chosenTask.numOfPoints === 0){
                        return
                    }
                    const data = {
                        userId: this.user.id,
                        taskId: this.chosenTaskId,
                        date: this.date.toISOString()
                    }
                    const resp = await PostsService.postLabUser(data)
                    this.show = false
                    await this.$router.go()

                } catch (e) {
                    this.errorCode = e.response.data
                }
                finally {
                    this.loading = false
                }
            },
            async goNextWeek() {
                this.startWeek = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(7))
                this.endWeek = new Date(this.endWeek.getTime() + this.getWeekInMilliseconds(7))
                try {
                    await this.getTasks()
                }
                catch (e) {
                    this.errorCode = e
                }
            },
            async goPrevWeek() {
                this.startWeek = new Date(this.startWeek.getTime() - this.getWeekInMilliseconds(7))
                this.endWeek = new Date(this.endWeek.getTime() - this.getWeekInMilliseconds(7))
                try {
                    await this.getTasks()
                }
                catch (e) {
                    this.errorCode = e
                }
            },
            getWeekInMilliseconds(id) {
                return 1000 * 3600 * 24 * id;
            },
          validate(e) {
            if (this.getDate(this.dayTo[e.day]) <= new Date() || this.getDate(this.dayTo[e.day]) > (new Date(new Date().getTime() + this.getWeekInMilliseconds(7)))) {
              this.errorCode = `Записаться на ${this.date.toLocaleDateString()} нельзя`
                return false;
            }
              if(this.chosenTask.numOfPoints === 0){
                  this.errorCode = 'Свободных мест для записи на задачу нет'
                  return false
              }
            return true;
          },
            setStartWeek() {
                const today = new Date()
                this.startWeek = new Date(today.getTime() - this.getWeekInMilliseconds(Number.parseInt(this.dayTo[today.getDay()])))
                this.endWeek = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(6))
            }

        },
        computed: {
            MondayTasks() {
                return [...this.tasks].filter(facility => facility.day == "1")
            },
            TuesdayTasks() {
                return [...this.tasks].filter(facility => facility.day == "2")
            },
            WednusdayTasks() {
                return [...this.tasks].filter(facility => facility.day == "3")
            },
            ThursdayTasks() {
                return [...this.tasks].filter(facility => facility.day == "4")
            },
            FridayTasks() {
                return [...this.tasks].filter(facility => facility.day == "5")
            },
            SaturdayTasks() {
                return [...this.tasks].filter(facility => facility.day == "6")
            },
            SundayTasks() {
                return [...this.tasks].filter(facility => facility.day == "0")
            },
            // chosenTask() {
            //     return this.tasks.find(t => t.id == this.chosenTaskId)
            // },

            Monday() {
                return this.getDate(0).toLocaleDateString()
            },
            Tuesday() {
                return this.getDate(1).toLocaleDateString()
            },
            Wednesday() {
                return this.getDate(2).toLocaleDateString()
            },
            Thursday() {
                return this.getDate(3).toLocaleDateString()
            },
            Friday() {
                return this.getDate(4).toLocaleDateString()
            },
            Saturday() {
                return this.getDate(5).toLocaleDateString()
            },
            Sunday() {
                return this.getDate(6).toLocaleDateString()
            },

            user() {
                return this.$store.getters['registration/User'];
            },
            week() {
                return `${this.startWeek.toLocaleDateString()} - ${this.endWeek.toLocaleDateString()}`
            },
            IsError(){
                return this.errorCode !== ''
            }
        },
        mounted() {
            this.setStartWeek();
            this.getTasks();
        }
    }
</script>

