<template>
    <div>
        <h1 style="margin-left: 500px;">Регистрация аудитории</h1>
        <div>
            <my-input class="inpt" placeholder="Название аудитории" v-model="WorkshopName" @input="click = false"></my-input>
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="validate">
            Название аудитории должно быть непустым
        </div>
        <div>
            <v-btn color="darksalmon" class="btn" @click="registration()">Зарегистрировать</v-btn>
        </div>

        <div>
            <div>
                <my-input class="search" placeholder="Название аудитории" v-model="SearchWorkshop"></my-input>
            </div>
            <div>
                <workshop-list :workshops="sortedAndSearchedPosts" @workshopChoose="workshopChoose($event)"></workshop-list>
            </div>
        </div>

    </div>
</template>

<script>
    import PostsService from "@/service/PostsService";

    export default {
        data() {
            return {
                WorkshopName: "",
                Workshops: [],
                SearchWorkshop: "",
                click: false
            }
        },
        methods: {
            async registration() {
                this.click = true
                if (this.validate)
                {
                    return
                }
                try {
                    await PostsService.postWorkshop(this.WorkshopName)
                        .then(() => this.getWorkshops())
                } catch (e) {
                    console.log(e.response?.data?.message);
                }
            },
            async getWorkshops() {
                try {
                    const response = await PostsService.getWorkshops()
                    this.Workshops = response.data
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            workshopChoose(e){
                const workshop = this.Workshops.find(w => w.id == e).сlassroomName
                this.$router.push({ name:"facility", params: { workshopId: e, workshop: workshop } })
            }
        },
        mounted() {
            this.getWorkshops();
        },
        computed: {
            sortedAndSearchedPosts() {
                return [...this.Workshops].filter(workshop => workshop.сlassroomName.toLowerCase().includes(this.SearchWorkshop.toLowerCase()))
            },
            validate(){
                return this.WorkshopName === '' && this.click
            }


        }
    }
</script>

<style scoped>
    .search{
        width: 80%;
        margin-top: 30px;
        margin-bottom: 30px;
    }
</style>