<template>
    <div>
        <select v-model="modelValue" @input="updateInput" class="select">
            <option v-for="option in options" :value="option.value">
                {{ option.text }}
            </option>
        </select>
    </div>
</template>

<script>
    export default {
        name: 'my-select',
        props: {
            modelValue: String,
            options: Array
        },
        methods: {
            updateInput(event) {
                this.$emit('update:modelValue', event.target.value)
            }
        }
    }
</script>

<style scoped>
.select{
    display: inline-block;
    padding: 10px 15px;
    margin-bottom: 10px;
    border: 1px solid darksalmon;
    background-color: white;
    min-width: 200px;
}
</style>