import { createRouter, createWebHashHistory } from 'vue-router'
import store from '../store'
import Home from '../views/Home.vue'
import Registration from '../views/Registration'
import Register from '../views/Register'
import Students from "@/views/Students";
import Schedule from "@/views/Schedule";
import WorkshopCreate from "@/views/WorkshopCreate";
import FacilityCreate from "@/views/FacilityCreate";
import TasksCreate from "@/views/TasksCreate";
import Activate from "@/views/Activate";
import Profile from "@/views/Profile";
import ChangeRoles from "@/views/ChangeRoles";
import Grades from "@/views/Grades";
import HomeView from "@/views/HomeView";
import RegisterUser from "@/views/RegisterUser";
import Estimate from "@/views/Estimate";
import UserGrades from "@/views/UserGrades";
import GetDatabase from "@/views/GetDatabase";
import GradesV2 from "@/views/GradesV2";

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/registration',
    name: 'Registration',
    component: Registration
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/studentslist',
    name: 'studentsList',
    component: Students,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/schedule',
    name: 'schedule',
    component: Schedule,
    meta: {
      requiresAuth: true,

    }
  },
  {
    path: '/workshop',
    name: 'workshop',
    component: WorkshopCreate,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/facility',
    name: 'facility',
    component: FacilityCreate,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/tasks',
    name: 'tasks',
    component: TasksCreate,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/activate/:link',
    name: 'activate',
    component: Activate
  },
  {
    path: '/activate/:name/:surname/:secondName/:link',
    name: 'RegisterUser',
    component: RegisterUser
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/students',
    name: 'students',
    component: ChangeRoles,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/grades',
    name: 'grades',
    component: Grades,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/homevue',
    name: 'homevue',
    component: HomeView,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/estimate',
    name: 'estimate',
    component: Estimate,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/usergrades',
    name: 'usergrades',
    component: UserGrades,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/db',
    name: 'getdb',
    component: GetDatabase,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/gradesv2',
    name: 'gradesv2',
    component: GradesV2,
    meta: {
      requiresAuth: true
    }
  },
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters['registration/isAuthed']) {
      next()
      return
    }
    next('/registration')
  } else {
    next()
  }
})

export default router
