<template>
    <div>
        <div>
            <div>
                <my-input class="search" placeholder="ФИО" v-model="SearchStudent" style="width: 80%"></my-input>
            </div>

            <div class="rr">
                <student-list :students="SortedStudents" @chooseStudent="chooseStudent($event)"></student-list>
            </div>
        </div>

        <my-dialog v-model:show="show" >
            <h1>Управление пользователем</h1>
            <div class="ma-2">
                ФИО пользователя: {{ this.user.name }} {{ this.user.surname }} {{ this.user.secondName == null ? '' : this.user.secondName }}
            </div>
            <div>
                <v-btn color="grey" @click="showDeleteDialog">Удалить пользователя</v-btn>
            </div>
            <div>
                <v-btn color="grey" @click="showChangeRoleDialog" style="margin-top: 15px;">Сменить роль</v-btn>
            </div>
        </my-dialog>

        <my-dialog v-model:show="showDeleteUser">
            <h1>Подтвердите удаление пользователя и всей информации, что с ним связана</h1>
            <v-btn @click="deleteUser" dark color="grey" class="mr-1">Подтвердить</v-btn>
            <v-btn @click="showDeleteUser = false" dark color="grey">Отмена</v-btn>
        </my-dialog>

        <my-dialog v-model:show="showChangeRole" >
            <h1>Смена роли пользователя</h1>
            <div class="dialog">Текущая роль: {{  this.options.find(p => p.value === this.user.role).text }}</div>
            <div class="dialog">Выбор нужной роли: </div>
            <my-select :options="options" v-model="chosen">

            </my-select>
            <v-btn color="grey" @click="change" style="margin-top: 15px;">Сменить роль</v-btn>
        </my-dialog>

        <div v-if="errorCode !== ''">
            {{ errorCode }}
        </div>
    </div>
</template>

<script>
    import UserService from "@/service/UserService";
    import StudentList from "@/components/StudentList";
    import MyInput from "@/components/UI/MyInput";
    import {mapActions} from "vuex";

    export default {
        components: {MyInput, StudentList},
        data(){
            return{
                students: [],
                show: false,
                showDeleteUser: false,
                showChangeRole: false,
                user: {},
                chosenUserId: null,
                options: [
                    {text: "Студент", value: "user"},
                    {text: "Преподаватель", value: "teacher"},
                    {text: "Администратор", value: "admin"},
                ],
                chosen: 'user',
                SearchStudent: '',
                errorCode: '',
                self: {}
            }
        },
        mounted() {
            this.self = this.$store.getters['registration/User'];
            this.getStudents();
        },
        methods: {
            ...mapActions({
                logout: 'registration/logout',
            }),
            async getStudents(){
                try {
                    const response = await UserService.getAll();
                    this.students = response.data;
                } catch (e) {
                    this.errorCode = e
                    console.log(e.response?.data?.message);
                }
            },
            async change(){
                try {
                    const user = this.students.find(p => p.id == this.chosenUserId)
                    const request = {
                        id: user.id,
                        user: user
                    }
                    request.user.role = this.chosen
                    const response = await UserService.putUser(request)
                    this.$store.dispatch('registration/updateUser')
                    this.getStudents();
                } catch (e) {
                    this.errorCode = e
                    console.log(e.response?.data?.message);
                }
            },
            chooseStudent(e){
                this.chosenUserId = e;
                this.user = this.students.find(p => p.id == this.chosenUserId)
                this.show = true;
            },
            showDeleteDialog(){
                this.showDeleteUser = true
                this.show = false
            },
            showChangeRoleDialog(){
                this.showChangeRole = true
                this.show = false
            },
            async deleteUser(){

                try {
                    await UserService.delete(this.chosenUserId)
                                     // .then(() => this.logout)
                    if (this.self.id === this.chosenUserId){
                        this.logout()
                    }
                    this.$router.go()
                }
                catch (e) {
                    this.errorCode = e
                }
            }
        },
        computed: {
            SortedStudents() {
                return [...this.students].filter(student => `${student.name} ${student.surname} ${student.secondName}`.toLowerCase().includes(this.SearchStudent.toLowerCase()))
            }
        }
    }
</script>

<style scoped>
    .dialog{
        margin: 10px;
    }
</style>