<template>
    <div>
        <div class="head">Регистрация</div>
        <div>
            <my-input placeholder="Фамилия" v-model="userSurname" @input="click=false"></my-input>
            <div class="inpt" v-if="validateSur"> Поле не должно быть пустым и может содержать только кириллицу</div>
        </div>
        <div>
            <my-input placeholder="Имя" v-model="userName" @input="click=false" ></my-input>
            <div class="inpt" v-if="validateName">Поле не должно быть пустым и может содержать только кириллицу</div>
        </div>
        <div>
            <my-input placeholder="Отчество" v-model="userSecondName" @input="click=false"></my-input>
        </div>
        <div>
            <my-input placeholder="Email" v-model="email" @input="click=false"></my-input>
            <div class="inpt" v-if="validateMail">Введите email правильно</div>
        </div>
        <div>
            <my-button class="btn" @click="reg">Зарегистрироваться</my-button>
        </div>
        <div>
            <my-router-link to="/registration">Назад</my-router-link>
        </div>
        <div style="margin-top: 10px">
            {{  message }}
        </div>
        <div style="margin-top: 10px" v-if="IsError">
             Ошибка: {{ error }}
        </div>
    </div>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: 'register',
        data(){
            return{
                userName: '',
                userSurname: '',
                userSecondName: '',
                email: '',
                login: '',
                message: '',
                error: '',
                click: false
            }
        },
        methods: {
            ...mapActions({
                registration: 'registration/registration',
            }),
            async reg(){
                this.click = true
                if (this.validateName || this.validateSur || this.validateMail){
                    return
                }
                else {
                    try {
                        this.error = ''
                        const obj = { secondname: this.userSecondName, surname: this.userSurname, name: this.userName, email: this.email }
                        const resp = await this.registration(obj)
                        this.message = resp;
                    }
                    catch (e) {
                        this.error = e.response.data
                    }
                }
            }
        },
        computed: {
            IsError(){
                return this.error !== ''
            },
            validateName(){
                // const valid = this.userName.match(/[\w\u0430-\u044f]+/ig);
                const valid = /^[а-яё]*$/i.test(this.userName);
                if ((!this.userName || !valid) && this.click){
                    return true
                }
                return false
            },
            validateSur(){
                const valid = /^[а-яё]*$/i.test(this.userSurname);
                if ((!this.userSurname || !valid) && this.click){
                    return true
                }
                return false
            },
            validateMid(){
                const valid = /^[а-яё]*$/i.test(this.userSecondName);
                if ((!this.userSecondName || !valid) && this.click){
                    return true
                }
                return false
            },
            validateMail(){
                const valid = this.email.toLowerCase()
                    .match(
                        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                    );
                if ((!this.email || !valid) && this.click){
                    return true
                }
                return false
            }

        }
    }
</script>

<style scoped>
.inpt{
    color: red;
}
    .head{
        font-size: x-large;
        margin-top: 30px;
    }
</style>