<template>
        <div class="Post" @click="gradeChoose">
            <div class="line" v-if="!grade.gradeConfirmed">Промежуточная оценка: {{ grades.find(p => p.value == grade.rating).text }}</div>
            <div class="line" v-if="grade.gradeConfirmed">Окончательная оценка: {{ grades.find(p => p.value == grade.rating).text }}</div>
            <div class="line">Преподаватель: {{ grade.teacherSurname }} {{ grade.teacherName }} <span v-if="grade.teacherMiddleName !== undefined">{{ grade.teacherMiddleName }}</span></div>
            <div class="line">Описание задачи: {{ grade.labUser.description }}</div>
            <div class="line" v-if="show">Дата задачи: {{ new Date(grade.labUser.date).toLocaleDateString() }}</div>
        </div>
</template>

<script>
    export default {
        name: "grade",
        data(){
            return{
                grades: [
                    {text: "Неявка", value: "1"},
                    {text: "Неудовлетворительно", value: "2"},
                    {text: "Удовлетворительно", value: "3"},
                    {text: "Хорошо", value: "4"},
                    {text: "Отлично", value: "5"},
                ],
            }
        },
        props: {
            grade: {
                type: Object,
                required: true
            },
            show: {
                type: Boolean,
                default: true
            }
        },
        methods: {
            gradeChoose(){
                this.$emit("gradeChoose", this.task)
            }
        },
        mounted(){
            console.log(this.grade)
        }

    }
</script>

<style scoped>
    .line{
        margin-bottom: 5px;
    }
    .Post{
        min-width: 200px;
        padding: 10px 15px;
        width: 24%;
        border: 2px solid #e9967a;
        margin: 5px;
    }

    .Post:hover{
        background-color: #f6cbba;
    }

</style>