<template>
    <div>
        <h1 style="margin-top: 50px">
            Название аудитории {{ Workshop }}
        </h1>
        <div>
            <v-btn color="grey" @click="show=true">
                Удалить Аудиторию
            </v-btn>
        </div>
        <h1>Регистрация установки в этой аудитории</h1>
        <div>
            <my-input placeholder="Название установки" v-model="Name" @input="this.click = false"   ></my-input>
        </div>
        <div>
            <my-input placeholder="Число мест на установке" v-model="NumOfPoints" @input="this.click = false"></my-input>
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="!validateNum">
            Число мест должно быть числовым значением!
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="!validateName">
            Название установки должно быть непустым
        </div>

        <div>
            <v-btn color="grey" @click="registration()">Зарегистрировать</v-btn>
        </div>
        <div>
            <div>
                <my-input class="search" placeholder="Название аудитории" v-model="SearchFacility"></my-input>
            </div>
            <div>
                <facility-list :facilities="sortedAndSearchedPosts" @chooseFacility="facilityChoose($event)"></facility-list>
            </div>
        </div>

        <my-dialog v-model:show="show">
            <h1>Подтвердите удаление аудитории и всей информации что с ней связано</h1>
            <v-btn @click="deleteWorkshop" dark color="grey" class="mr-1">Подтвердить</v-btn>
            <v-btn @click="show = false" dark color="grey">Отмена</v-btn>
        </my-dialog>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService"
    import FacilityList from "@/components/FacilityList";

    export default {
        components: {FacilityList},
        data(){
            return{
                Name: "",
                NumOfPoints: "",
                NumOfFacilities: "",
                WorkshopId: "",
                Workshop: "",
                SearchFacility: "",
                Facilities: [],
                click: false,
                show: false
            }
        },
        methods: {
            async registration(){
                this.click = true
                if (!this.validateName || !this.validateNum || this.WorkshopId === ''){
                    return
                }
                try {
                    // let arr = [];
                    // for (let i = 0; i < this.NumOfFacilities; i++){
                    //     arr.push(PostsService.postFacility({num: this.NumOfPoints, workshopId: this.WorkshopId, name: this.Name}))
                    // }
                    // const response = await Promise.all(arr)
                    const response = await PostsService.postFacility({num: this.NumOfPoints, workshopId: this.WorkshopId, name: this.Name})
                    .then(p => this.getWorkshops())
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            async getWorkshops(){
                try {
                    const response = await PostsService.getFacilities()
                    this.Facilities = response.data
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            facilityChoose  (e){
                const facility = this.Facilities.find(f => f.id == e).name
                this.$router.push({ name:"tasks", params: { facilityId: e, facility: facility } })
            },
            async deleteWorkshop(){
                try {
                    await PostsService.deleteWorkshop(this.WorkshopId)
                    await this.$router.push({name: 'workshop'})
                }
                catch (e) {
                    this.errorCode = e
                }
            }

        },
        mounted() {
                this.WorkshopId = this.$route.params.workshopId;
                this.Workshop = this.$route.params.workshop;
                if (this.WorkshopId == undefined || this.Workshop == undefined){
                    this.$router.push({ name: 'workshop' })
                }
                this.getWorkshops();
        },
        computed: {
            sortedAndSearchedPosts() {
                if (this.WorkshopId === undefined){
                    return [...this.Facilities].filter(facility => facility.name.toLowerCase().includes(this.SearchFacility.toLowerCase()))
                }
                return [...this.Facilities].filter(facility => facility.name.toLowerCase().includes(this.SearchFacility.toLowerCase()) && facility.workshop.id == this.WorkshopId)
            },
            validateNum(){
                const nums = "0123456789"
                if (!this.NumOfPoints){
                    return !this.click
                }
                for (let i = 0; i < this.NumOfPoints.length; i++) {
                    if (!nums.includes(this.NumOfPoints.charAt(i))){
                        return !this.click
                    }
                }

                return true
            },
            validateName(){
                if (this.Name === ''){
                    return !this.click
                }
                return true
            }
        }

    }
</script>

<style scoped>
.search{
    width: 80%;
    margin-top: 30px;
    margin-bottom: 30px;
}
</style>