<template>
    <div class="rasp">
        <vue-cal xsmall
                 :time="false"
                 locale="ru"
                 :selected-date="date"
                 hide-view-selector
                 class="vuecal--green-theme"
                 events-dash-on-year-view
                 active-view="month"
                 :disable-views="['years', 'year', 'week', 'day']"
                 style="max-width: 300px;height: 300px"
                 :events="events"
                 @cell-click="$emit('cell-click', $event)">
        </vue-cal>

    </div>
</template>

<script>
    export default {
        name: 'v-rasp',
        props: {
            events: {
                type: Array,
                required: true
            }
        },
        data(){
            return{
                date: new Date().toISOString().split('T')[0]
            }
        },
        methods: {
            log(e){
                console.log(e)
            }
        }
    }
</script>

<style>
    .rasp{
        display: flex;
        padding: 10px;
        justify-content: center;
    }
</style>