<template>
    <div class="hh">
        <h1 class="header">
            <span class="clickable" @click="goPrevWeek">&lt;</span>
            Неделя {{ week }}
            <span class="clickable" @click="goNextWeek">></span>
        </h1>
    </div>
    <div class="home task"
    @click="ToMonday">
        <div>
            <h1>
                Понедельник
            </h1>
            <div>
                {{ Monday }}
            </div>
        </div>
    </div>

    <div class="home task"
    @click="ToTuesday">
        <h1>Вторник</h1>
        <div>
            {{ Tuesday }}
        </div>
    </div>

    <div class="home task"
    @click="ToWednesday">
        <h1>Среда</h1>
        <div>
            {{ Wednesday }}
        </div>
    </div>

    <div class="home task"
    @click="ToThursday">
        <h1>Четверг</h1>
        <div>
            {{ Thursday }}
        </div>
    </div>

    <div class="home task"
    @click="ToFriday">
        <h1>Пятница</h1>
        <div>
            {{ Friday }}
        </div>
    </div>

    <div class="home task"
    @click="ToSaturday">
        <h1>Суббота</h1>
        <div>
            {{ Saturday }}
        </div>
    </div>

    <div class="home task"
    @click="ToSunday">
        <h1>Воскресенье</h1>
        <div>
            {{ Sunday }}
        </div>
    </div>

</template>

<script>
    import {mapActions} from "vuex";
    import PostsService from "../service/PostsService";

    export default {
        name: 'Grades',
        data() {
            return {
                chosenTaskId: null,
                chosenTask: {},
                date: undefined,
                startWeek: new Date('2022-06-22'),
                endWeek: new Date('2022-06-29'),
                dayTo: {
                    "1": "0",
                    "2": "1",
                    "3": "2",
                    "4": "3",
                    "5": "4",
                    "6": "5",
                    "0": "6",
                },
            }
        },
        methods: {
            async ToSunday(){
               await this.$router.push({name: 'estimate', params: {date: this.Sunday, day: 'Воскресенье'}})
            },
            async ToSaturday(){
                await this.$router.push({name: 'estimate', params: {date: this.Saturday, day: 'Суббота'}})
            },
            async ToFriday(){
                await this.$router.push({name: 'estimate', params: {date: this.Friday, day: 'Пятница'}})
            },
            async ToThursday(){
                await this.$router.push({name: 'estimate', params: {date: this.Thursday, day: 'Четверг'}})
            },
            async ToWednesday(){
                await this.$router.push({name: 'estimate', params: {date: this.Wednesday, day: 'Среда'}})
            },
            async ToTuesday(){
                await this.$router.push({name: 'estimate', params: {date: this.Tuesday, day: 'Вторник'}})
            },
            async ToMonday(){
                await this.$router.push({name: 'estimate', params: {date: this.Monday, day: 'Понедельник'}})
            },
            getDate(id) {
                const date = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(id))
                return date
            },
            async goNextWeek() {
                this.startWeek = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(7))
                this.endWeek = new Date(this.endWeek.getTime() + this.getWeekInMilliseconds(7))
            },
            async goPrevWeek() {
                this.startWeek = new Date(this.startWeek.getTime() - this.getWeekInMilliseconds(7))
                this.endWeek = new Date(this.endWeek.getTime() - this.getWeekInMilliseconds(7))
            },
            getWeekInMilliseconds(id) {
                return 1000 * 3600 * 24 * id;
            },
            // validate(e) {
            //     if (this.getDate(this.dayTo[e.day]) <= new Date() || this.getDate(this.dayTo[e.day]) > (new Date(new Date().getTime() + this.getWeekInMilliseconds(7)))) {
            //         this.errorCode = `Записаться на ${this.date.toLocaleDateString()} нельзя`
            //         return false;
            //     }
            //     if(this.chosenTask.numOfPoints === 0){
            //         this.errorCode = 'Свободных мест для записи на задачу нет'
            //         return false
            //     }
            //     return true;
            // },
            setStartWeek() {
                const today = new Date()
                this.startWeek = new Date(today.getTime() - this.getWeekInMilliseconds(Number.parseInt(this.dayTo[today.getDay()])))
                this.endWeek = new Date(this.startWeek.getTime() + this.getWeekInMilliseconds(6))
            }

        },
        computed: {

            Monday() {
                return this.getDate(0).toLocaleDateString()
            },
            Tuesday() {
                return this.getDate(1).toLocaleDateString()
            },
            Wednesday() {
                return this.getDate(2).toLocaleDateString()
            },
            Thursday() {
                return this.getDate(3).toLocaleDateString()
            },
            Friday() {
                return this.getDate(4).toLocaleDateString()
            },
            Saturday() {
                return this.getDate(5).toLocaleDateString()
            },
            Sunday() {
                return this.getDate(6).toLocaleDateString()
            },

            week() {
                return `${this.startWeek.toLocaleDateString()} - ${this.endWeek.toLocaleDateString()}`
            },
        },
        mounted() {
            this.setStartWeek();
        }
    }
</script>

<style scoped>
    .home {
        float: left;
        width: calc(1 / 8 * 100%);
        margin-left: 20px;
    }

    .task {
        border: #f6cbba solid 1px;
        margin-bottom: 10px;
    }
    .task:hover {
        background-color: #f6cbba;
    }

    .clickable {
        user-select: none;
    }
    .hh{
        width: 100%;
        justify-content: center;
        display: flex;
    }
    .header{
        margin: 10px;
    }
</style>





