<template>
    <div>
        <h1 style="margin-top: 50px">
            Название установки {{ Facility }}
        </h1>
        <div>
            <v-btn color="grey" @click="showH=true">
                Удалить установку
            </v-btn>
            <div v-if="IsError" class="error">
                {{ errorCode }}
            </div>
        </div>
        <h1>Постановка задач</h1>

        <div>
            <my-select v-model="Day" :options="Days">

            </my-select>
        </div>
        <div>
            <my-input placeholder="Описание задачи" v-model="TaskDescription"></my-input>
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="validate"
             @input="click = false">
            Описание задачи должно быть непустым
        </div>
        <div>
            <v-btn color="grey" @click="registration()">Зарегистрировать</v-btn>
        </div>
        <div>
            <div>
                <my-input class="search" placeholder="Описание задачи" v-model="SearchTask"></my-input>
            </div>
            <div class="rr">
                <tasks-list :tasks="sortedAndSearchedPosts" @taskChose="taskChose"></tasks-list>
            </div>
        </div>

        <my-dialog v-model:show="show">
            <h1>Запись на задачу</h1>
            <div class="dialog">Описание задачи: {{ chosenTask.description }}</div>
            <v-btn @click="deleteTask" dark color="grey">Удалить задачу</v-btn>
            <div v-if="IsError">
                Ошибка: {{ errorCode }}
            </div>
        </my-dialog>

        <my-dialog v-model:show="showH">
            <h1>Подтвердите удаление установки и всей информации, что с ней связана</h1>
            <v-btn @click="deleteFacility" dark color="grey" class="mr-1">Подтвердить</v-btn>
            <v-btn @click="showH = false" dark color="grey">Отмена</v-btn>
        </my-dialog>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService"
    import TasksList from "@/components/TasksList";

    export default {
        components: {TasksList},
        data() {
            return {
                Day: "Понедельник",
                TaskDescription: "",
                NumOfFacilities: "",
                FacilityId: "",
                SearchTask: "",
                Tasks: [],
                Facility: '',
                Days: [
                    {text: "Понедельник", value: "Понедельник"},
                    {text: "Вторник", value: "Вторник"},
                    {text: "Среда", value: "Среда"},
                    {text: "Четверг", value: "Четверг"},
                    {text: "Пятница", value: "Пятница"},
                    {text: "Суббота", value: "Суббота"},
                    {text: "Воскресенье", value: "Воскресенье"},
                ],
                DayOfWeek: {
                    "Воскресенье": 0,
                    "Понедельник": 1,
                    "Вторник": 2,
                    "Среда": 3,
                    "Четверг": 4,
                    "Пятница": 5,
                    "Суббота": 6
                },
                click: false,
                show: false,
                DayOfWeekTask: {
                    "0": "Воскресенье",
                    "1": "Понедельник",
                    "2": "Вторник",
                    "3": "Среда",
                    "4": "Четверг",
                    "5": "Пятница",
                    "6": "Суббота"
                },
                showH: false,
                errorCode: '',
                chosenTask: {}
            }
        },
        methods: {
            async registration() {
                this.click = true
                if (this.TaskDescription === '') {
                    return
                }
                try {
                    await PostsService.postTask({description: this.TaskDescription, facilityId: this.FacilityId, day: this.DayOfWeek[this.Day]})
                    await this.getTasks();
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            async getTasks() {
                try {
                    const response = await PostsService.getAllTasks()
                    this.Tasks = response.data
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            taskChose(e) {
                this.errorCode = ''
                this.show = true
                this.chosenTask = e
            },
            async deleteFacility() {
                try {
                    await PostsService.deleteFacility(this.FacilityId)
                    await this.$router.push({name: 'workshop'})
                } catch (e) {
                    this.errorCode = e
                }
            },
            async deleteTask() {
                const taskId = this.chosenTask?.id
                const index = this.Tasks.findIndex(t => t.id === taskId)
                if (taskId == null) {
                    this.errorCode = 'taskId == null'
                    return
                }
                try {
                    await PostsService.deleteTask(taskId)
                    this.Tasks.splice(index, 1)
                    this.show = false
                } catch (e) {
                    this.errorCode = e
                }
            }
        },
        mounted() {
            this.FacilityId = this.$route.params.facilityId
            this.Facility = this.$route.params.facility
            if (this.FacilityId === undefined || this.Facility === undefined) {
                this.$router.push({name: 'workshop'})
            }
            this.getTasks();
        },
        computed: {
            sortedAndSearchedPosts() {
                if (this.FacilityId === undefined) {
                    return [...this.Tasks].filter(task => task.description.toLowerCase().includes(this.SearchTask.toLowerCase()))
                }
                return [...this.Tasks].filter(task => task.description.toLowerCase().includes(this.SearchTask.toLowerCase()) && task.facilityId == this.FacilityId)
            },
            validate() {
                return this.TaskDescription === '' && this.click
            },
            IsError() {
                return this.errorCode !== ''
            }
        }

    }
</script>

<style scoped>
    .search {
        width: 80%;
        margin-top: 30px;
        margin-bottom: 30px;
    }

    .rr {
        width: 60%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
    .error{
        margin: 10px;
        color: red;
    }
</style>