<template>
    <div class="wrap">
        <div>
            <div class="day">
                <div>
                    День недели: {{day}}
                </div>
                <div>
                    Дата: {{date}}
                </div>
            </div>

            <div>
                <my-input class="search" placeholder="ФИО" v-model="SearchStudent"></my-input>
            </div>

            <div class="rr">
                <student-list :students="SortedStudents" @chooseStudent="chooseStudent($event)"></student-list>
            </div>

            <div v-if="SortedStudents.length === 0">
                Записей студентов на {{this.date}} нет
            </div>
        </div>

        <my-dialog v-model:show="showStuds">
            <h1>Проставление оценок</h1>
            <div class="dialog">ФИО студента: {{ this.chosenUser.surname }} {{this.chosenUser.name}}
                <span v-if="this.chosenUser.secondname!==undefined">{{ this.chosenUser.secondname }}</span>
            </div>
            <div v-if="labGrade != null">
                <span v-if="!this.labGrade.gradeConfirmed">Промежуточная оценка: {{options.find(o => Number.parseInt(o.value) === this.labGrade.rating).text }}</span>
                <span v-if="this.labGrade.gradeConfirmed">Окончательная оценка: {{options.find(o => Number.parseInt(o.value) === this.labGrade.rating).text }}</span>
            </div>
            <div class="dialog">Описание задачи: {{ `${this.task.description}` }}</div>
            <my-select :options="options" v-model="chosen">

            </my-select>
            <div>
                <v-btn @click="change" color="grey" style="margin-top: 15px;">Поставить промежуточную оценку</v-btn>
            </div>
            <div>
                <v-btn @click="changeConfirmed" color="grey" style="margin-top: 15px;">Поставить окончательную оценку</v-btn>
            </div>
            <div v-if="IsError">
                Ошибка: {{errorCode}}
            </div>
        </my-dialog>

        <my-dialog v-model:show="showTasks">
            <h1>Выбор задачи</h1>
            <div v-if="Tasks.length === 0">
                Список неоцененных задач пользователя пуст
            </div>
            <div v-if="IsError">
                Ошибка: {{errorCode}}
            </div>
            <subscribe-list :subscribes="Tasks" @unsubscribe="taskChose($event)"></subscribe-list>
<!--            <tasks-list :tasks="Tasks" @taskChose="taskChose($event)" class="newClass"></tasks-list>-->
        </my-dialog>

    </div>
</template>

<script>
    import UserService from "@/service/UserService";
    import StudentList from "@/components/StudentList";
    import MyInput from "@/components/UI/MyInput";
    import PostsService from "@/service/PostsService";
    import SubscribeList from "@/components/SubscribeList";

    export default {
        components: {SubscribeList, MyInput, StudentList},
        data(){
            return{
                students: [],
                showStuds: false,
                showTasks: false,
                user: {},
                chosenUser: {},
                task: {},
                chosenUserId: null,
                chosenTaskId: null,
                options: [
                    {text: "Неявка", value: "1"},
                    {text: "Неудовлетворительно", value: "2"},
                    {text: "Удовлетворительно", value: "3"},
                    {text: "Хорошо", value: "4"},
                    {text: "Отлично", value: "5"},
                ],
                chosen: '1',
                SearchStudent: '',
                Tasks: [],
                errorCode: '',
                loader: false,
                labGrade: {},
                day: undefined,
                date: undefined
            }
        },
        mounted() {
            const date = this.$route.params.date
            this.date = date
            this.day = this.$route.params.day
            if (this.date === undefined || this.day === undefined){
                this.$router.push({name: 'grades'})
            }

            this.user = this.$store.getters['registration/User'];
            this.getStudents();
        },
        methods: {
            async getStudents(){
                const string = this.date.split(".")

                const year = Number.parseInt(string[2])
                const month = Number.parseInt(string[1])
                const day = Number.parseInt(string[0])
                const date = new Date(year,month - 1,day + 1).toISOString()

                try {
                    const response = await UserService.getUsersOnThisDay(date);
                    this.students = response.data;
                } catch (e) {
                    this.errorCode = e.response
                }
            },

            async change(){
                try {
                    const grade = {
                        rating: Number.parseInt(this.chosen),
                        teacherId: this.user.id,
                        labUserId: this.chosenTaskId
                    }
                    const request = {
                        labId: this.chosenTaskId,
                        grade: grade
                    }
                    const response = await UserService.estimate(request)
                    this.showStuds = false;
                } catch (e) {

                    this.errorCode = 'Студенту уже поставлена оценка'
                    console.log(e.response?.data?.message);
                }
            },
            async changeConfirmed(){
                try {
                    const grade = {
                        rating: Number.parseInt(this.chosen),
                        teacherId: this.user.id,
                        labUserId: this.chosenTaskId,
                        gradeConfirmed: true
                    }
                    const request = {
                        labId: this.chosenTaskId,
                        grade: grade
                    }
                    const response = await UserService.estimate(request)
                    this.showStuds = false;
                } catch (e) {

                    this.errorCode = 'Студенту уже поставлена оценка'
                    console.log(e.response?.data?.message);
                }
            },
            async chooseStudent(e){
                this.errorCode = ''
                this.showTasks = true;
                this.chosenUserId = e;
                this.chosenUser = this.students.find(p => p.id === e)

                try {
                    this.loader = true;
                    const response = await PostsService.getUserSubscribes(e)
                    this.Tasks = response.data.filter(p => this.compareDate(p.date, this.date))
                }
                catch (e) {
                    this.errorCode = e
                    console.log(e?.data)
                }
                finally {
                    this.loader = false
                }

                this.user = this.$store.getters['registration/User'];
            },
            compareDate(str, second){
                const date = new Date(str)
                const year = date.getFullYear()
                const month = date.getMonth()
                const day = date.getDate()

                const string = second.split(".")
                const secYear = Number.parseInt(string[2])
                const secMonth = Number.parseInt(string[1])
                const secDay = Number.parseInt(string[0])
                const secDate = new Date(secYear,secMonth - 1,secDay)

                if (date.getFullYear() === secDate.getFullYear() &&
                    date.getMonth() === secDate.getMonth() &&
                    date.getDate() === secDate.getDate()){
                    return true
                }
                return false
            },
            taskChose(e){
                this.errorCode = ''
                this.task = this.Tasks.find(p => p.id === e).task
                this.labGrade = this.Tasks.find(p => p.id === e).labGrade
                this.chosenTaskId = e
                this.showStuds = true;
                this.showTasks = false;
            },
        },
        computed: {
            SortedStudents() {
                return [...this.students]
                    .filter(s => s.role === "user")
                    .filter(student => `${student.name} ${student.surname} ${student.secondName}`.toLowerCase().includes(this.SearchStudent.toLowerCase()))
            },
            IsError(){
                return this.errorCode !== ''
            },
            Date(){
                return this.date.toLocaleDateString()
            }
        }
    }
</script>

<style scoped>
    .dialog{
        margin: 10px;
    }

    .day{
    }

    .newClass{
        display: flex;
        flex-wrap: wrap;
    }
    .wrap{
        margin-top: 30px;
    }
    .search{
        margin-top: 50px;
    }
</style>


