<template>
    <div class="wrap">
        <div>
            <div>
                <my-input class="search" placeholder="ФИО" v-model="SearchStudent"></my-input>
            </div>

            <div class="rr">
                <student-list :students="SortedStudents" @chooseStudent="chooseStudent($event)"></student-list>
            </div>

        </div>

        <my-dialog v-model:show="showStuds">
            <h1>Календарь активности студента</h1>
            <div class="dialog">ФИО студента: {{ `${this.chosenUser.surname} ${this.chosenUser.name} ${this.chosenUser.secondname == null ? '' : this.chosenUser.secondname }` }}</div>
<!--            <grade-list :grades="grades"></grade-list>-->
            <v-rasp @cell-click="chooseEvent($event)" :events="Events">

            </v-rasp>
        </my-dialog>

        <my-dialog v-model:show="showGrades">
            <h1>Проставленные оценки</h1>
            <div class="dialog">ФИО студента: {{ `${this.chosenUser.surname} ${this.chosenUser.name} ${this.chosenUser.secondname == null ? '' : this.chosenUser.secondname }` }}</div>
            <div class="dialog">Дата: {{ this.chosenDate.toLocaleDateString() }}</div>
            <grade-list :show="false" :grades="Grades"></grade-list>
            <div v-if="Grades.length === 0 && !loader">
                Список оценок пуст
            </div>
        </my-dialog>
        <div v-if="SortedStudents.length === 0 && !loader">
            Список студентов пуст
        </div>
        <div v-if="IsError">
            Ошибка: {{errorCode}}
        </div>
        <div v-if="loader">
            Загрузка...
        </div>
    </div>
</template>

<script>
    import UserService from "@/service/UserService";
    import StudentList from "@/components/StudentList";
    import MyInput from "@/components/UI/MyInput";
    import PostsService from "@/service/PostsService";
    import SubscribeList from "@/components/SubscribeList";
    import GradeList from "@/components/GradeList";

    export default {
        components: {GradeList, SubscribeList, MyInput, StudentList},
        data(){
            return{
                students: [],
                showStuds: false,
                showGrades: false,
                user: {},
                chosenUser: {},
                chosenUserId: null,
                chosen: '1',
                SearchStudent: '',
                grades: [],
                errorCode: '',
                loader: false,
                chosenDate: undefined
            }
        },
        mounted() {

            this.user = this.$store.getters['registration/User'];
            this.getStudents();
        },
        methods: {
            async getStudents(){
                this.loader = true
                try {
                    const response = await UserService.getAll();
                    this.students = response.data;
                } catch (e) {
                    this.errorCode = e.response
                }
                finally {
                    this.loader = false
                }
            },

            async change(){

            },
            async chooseStudent(e){
                this.showStuds = true;
                this.chosenUserId = e;
                this.chosenUser = this.students.find(p => p.id === e)
                this.loader = true;

                try {
                    const response = await PostsService.getGrades(e)
                    this.grades = response.data
                }
                catch (e) {
                    this.errorCode = e
                    console.log(e?.data)
                }
                finally {
                    this.loader = false
                }

                this.user = this.$store.getters['registration/User'];
            },
            chooseEvent(e){
                this.chosenDate = e
                this.showGrades = true
                this.showStuds = false
            }

        },
        computed: {
            SortedStudents() {
                return [...this.students]
                    .filter(s => s.role === "user")
                    .filter(student => `${student.name} ${student.surname} ${student.secondName}`.toLowerCase().includes(this.SearchStudent.toLowerCase()))
            },
            IsError(){
                return this.errorCode !== ''
            },
            Events() {
                return [...this.grades].map(g => ({
                    start: new Date(g.labUser.date).toISOString().split('T')[0],
                    end: new Date(g.labUser.date).toISOString().split('T')[0],
                    grade: g
                }))
            },
            Grades() {
                // return [...this.grades].filter(g =>
                //     new Date(new Date(g.labUser.date.split('T')[0]).getFullYear()
                //             ,new Date(g.labUser.date.split('T')[0]).getMonth()
                //             ,new Date(g.labUser.date.split('T')[0]).getDate()) === new Date(this.chosenDate.getFullYear(),this.chosenDate.getMonth(),this.chosenDate.getDate()))
                return [...this.grades].filter(g => new Date(g.labUser.date).toLocaleDateString() === this.chosenDate.toLocaleDateString())
            }
        }
    }
</script>

<style scoped>
    .dialog{
        margin-top: 10px;
        margin-right: auto;
        margin-left: auto;
    }

    .day{
    }

    .wrap{
        margin-top: 30px;
    }
    .search{
        margin-top: 50px;
    }
</style>


