import $api from "../http";

export default class UserService {
    static async getAll() {
        return $api.get('/users')
    }

    static async getUser() {
        return $api.get('/users/self')
    }

    static async getUsersOnThisDay(date) {
        return $api.get('/users/day', {params: {date: date}})
    }

    static async delete(id) {
        return $api.delete('/users/' + id)
    }

    static async putUser(data) {
        return $api.put('/users', data.user)
    }

    static async estimate(data) {
        return $api.put('/labusers/estimate/' + data.labId, data.grade)
    }
}