import $api from "../http";

export default class PostsService {
    static async postDataBase(email){
        return $api.post('/Grades/sendto', {}, {responseType: "blob"})
    }
    static async getWorkshops(){
        return $api.get('/workshops/short')
    }
    static async getGrades(id){
        return $api.get('/grades', {params: {id: id}})
    }
    static async getFacilities(){
        return $api.get('/facilities')
    }
    static async postWorkshop(data){
        return $api.post('/workshops', {сlassroomName: data})
    }
    static async getTasks(date){
        return $api.get('/praktikumtasks', {params: {date: date}})
    }
    static async getAllTasks(date){
        return $api.get('/praktikumtasks/all')
    }
    static async getAllLabUsers(){
        return $api.get('/LabUsers')
    }
    static async getUserSubscribes(id){
        return $api.get('/LabUsers/user/' + id)
    }
    static async getUserUnconfirmedSubscribes(id){
        return $api.get('/LabUsers/user/unconfirmed/' + id)
    }
    static async postFacility(data){
        return $api.post('/facilities', {numOfPoints: data.num, workshopId: data.workshopId, name: data.name})
    }
    static async postTask(data){
        return $api.post('/PraktikumTasks', {description: data.description, facilityId: data.facilityId, day: data.day })
    }
    static async postLabUser(data){
        return $api.post('/LabUsers', {userId: data.userId, taskId: data.taskId, date: data.date})
    }
    static async unsubscribe(id){
        return $api.delete('/LabUsers/' + id)
    }
    static async deleteWorkshop(id){
        return $api.delete('/workshops/' + id)
    }
    static async deleteFacility(id){
        return $api.delete('/facilities/' + id)
    }
    static async deleteTask(id){
        return $api.delete('/praktikumtasks/' + id)
    }

}