<template>
    <div class="post">
        <grade
                v-for="grade in grades"
                :grade="grade"
                :show="show"
                :key="grade.labUserId"
                @gradeChoose="gradeChoose"

        >

        </grade>
    </div>
</template>

<script>
    import Grade from "@/components/Grade";

    export default {
        name: 'grade-list',
        components: {Grade},
        props: {
            grades: {
                type: Array,
                required: true
            },
            show: {
                type: Boolean,
                required: false
            }
        },
        methods: {
            gradeChoose(id){
                this.$emit("gradeChoose", id)
            }
        }
    }
</script>

<style scoped>
    .post{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
</style>