<template>
    <div>

    </div>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        methods: {
            ...mapActions({
                activate: 'registration/activate',
            }),
        },
        mounted() {
            this.activate({
                name: this.$route.params.name,
                surname: this.$route.params.surname,
                secondName: this.$route.params.secondName,
                link: this.$route.params.link,
            })
                .then(() => this.$router.push({name: 'Home'}));
        }
    }
</script>

